.gallery {
  max-width: 1680px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.photo-card {
  margin-bottom: 100px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info-item {
  display: inline-block;
}

.load-more {
  visibility: hidden;
}

.search-form {
  background: linear-gradient(#1a2455, #ddd);
  border: 1px solid #ccc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.search-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  text-indent: -9999px;
  background-image: url("search.1a5dde6a.png");
  background-position: center;
  background-repeat: no-repeat;
  border-color: #1a2455;
  border-radius: 10px;
}

.input-style {
  height: 30px;
  border-color: #1a2455;
  border-radius: 10px;
}

